/* VARIABLES */

:root {
  --color-blue: #232d4b;
  --color-pink: #ff5470;
  --color-slate: #273142;
}

/* MAIN STYLES */

body {
  font-family: "PT Serif", "Georgia", sans-serif;
  padding: 0;
  margin: 0;
  line-height: 1.85em;
}

img {
  width: 100%;
  border: none;
  position: relative;
  display: block;
  z-index: 2000;
}

main a:not(.button):not(.fa):not(.arrow) {
  padding-left: 2px;
  padding-right: 2px;
  color: black;
  text-decoration: none;
  transition: color 0.5s ease-in-out;
}

main a:not(.button):not(.arrow):focus,
main a:not(.button):not(.arrow):hover {
  background: var(--color-blue);
  color: white;
  transition: all 0.25s;
}

code {
  font-family: "IBM Plex Mono", Consolas, "Courier New", monospace;
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}

h1 {
  padding-bottom: 0.5em;
  font-weight: 600;
  font-size: 300%;
  line-height: 1.15em;
}

h4 {
  margin-bottom: 0;
  text-transform: uppercase;
  font-size: 95%;
  margin-top: 2.5em;
  margin-bottom: 1.5em;
  line-height: 1.25em;
}

h4 + h5 {
  line-height: 1.5em;
  margin-top: -1em;
  margin-bottom: 0;
  color: gray;
}

h4 a,
h5 a {
  background: rgba(255, 84, 112, 0.4) !important;
}

h4 a:focus,
h4 a:hover,
h5 a:focus,
h5 a:hover {
  background: var(--color-pink) !important;
  color: white !important;
}

h4 a:focus::after,
h4 a:hover::after,
h5 a:focus::after,
h5 a:hover::after {
  content: "→";
}

span a {
  color: rgb(90, 134, 173);
  text-decoration: none;
  transition: color 0.5s ease-in-out;
}

span a:hover {
  background-color: var(--color-blue);
  color: white;
  transition: all 0.25s;
}

.class-names {
  color: rgb(90, 134, 173);
}

@media screen and (max-width: 640px) {
  body {
    font-size: 95%;
  }

  h1 {
    padding-bottom: 0;
  }

  .quarter > h4:first-child,
  .half > h4:first-child {
    margin-top: 1.5em;
  }
}

.caption {
  font-size: 85%;
  line-height: 1.5em;
}

.description {
  font-size: 95%;
  line-height: 1.5em;
}

a.anchor {
  padding: 0 !important;
}

.blue {
  color: var(--color-blue);
}

.blue-block {
  color: white;
  font-weight: bold;
  background-color: var(--color-blue);
}

.pink-block {
  color: white;
  font-weight: bold;
  background-color: var(--color-pink);
}

.small-text {
  font-size: 70%;
  display: inline;
}

/* HEADER and FOOTER STYLES */

header {
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
  padding-top: 3em;
}

header a {
  color: black;
  text-decoration: none;
  transition: color 0.5s ease-in-out;
}

header a:hover {
  color: var(--color-blue);
  transition: all 0.25s;
}

.site-title {
  z-index: 2000;
  text-align: center;
  font-size: 350%;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  font-weight: 600;
  padding-bottom: 0.33em;
}

nav {
  z-index: 2000;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

nav a {
  font-size: 85%;
  letter-spacing: 0.05em;
  margin: 2em;
  text-align: center;
}

nav a:hover {
  background-color: #232d4b;
  color: white;
  transition: all 0.25s;
}

footer {
  padding: 0 auto;
  display: flex;
  align-items: center;
  padding: 3em;
  text-align: center;
}

footer > * {
  display: block;
  z-index: 2000;
}

@media screen and (max-width: 640px) {
  header,
  footer {
    padding: 1.5em;
  }

  header {
    position: relative;
    display: block;
    z-index: 2000;
  }

  footer p {
    padding: 0;
    margin: 0;
  }

  footer a {
    color: white;
    background-color: black;
  }

  nav {
    margin-top: 18px;
  }

  nav a:first-child {
    margin-left: 0;
  }
}

/* LISTS */

ul {
  margin-bottom: 1em;
  list-style: outside none;
  margin-left: 0;
  padding-left: 2em;
}

li {
  font-size: 100%;
}

li a {
  color: rgb(90, 134, 173);
  text-decoration: none;
  transition: color 0.5s ease-in-out;
}

li a:hover {
  background-color: var(--color-blue);
  color: white;
  transition: all 0.25s;
}

ul li >,
ol li > * {
  padding: 0;
  margin: 0;
}

.list-bold li {
  font-weight: bold;
}

.list-plus li:before {
  content: "+";
  padding-right: 1em;
}

.list-arrows li:before {
  content: "→ ";
  padding-right: 1em;
}

.list-bar {
  padding-left: 0;
  margin-top: 12px;
  margin-bottom: 12px;
  line-height: 2em;
}

.list-bar li {
  display: inline;
  padding: 0.25em 0.5em;
  color: white;
}

.list-bar.actions li {
  background-color: var(--color-slate);
}

.list-bar.tools li {
  background-color: var(--color-pink);
}

.list-bar.buttons li {
  transition: all 0.25s ease-in-out;
}

.list-bar.buttons li:hover {
  opacity: 0.5;
  transition: all 0.25s;
}

.list-bar.buttons li a {
  color: white;
  text-decoration: none;
}

/* LAYOUT: CONTAINERS*/

main {
  padding-top: 1em;
}

.narrow {
  max-width: 480px;
  padding: 1.5em 3em;
  margin: 0 auto;
}

.wide {
  max-width: 768px;
  padding: 1.5em 3em;
  margin-left: auto;
  margin-right: auto;
}

.inset {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 1.5em 3em;
}

.bleed {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.cut {
  max-width: 75%;
}

@media screen and (max-width: 640px) {
  .wide,
  .narrow {
    padding: 0.75em 1.5em;
  }

  .inset {
    padding: 0;
  }

  .wide > *:first-child,
  .narrow > *:first-child,
  .inset > *:first-child {
    margin-top: 0;
  }

  .wide > *:last-child,
  .narrow > *:last-child,
  .inset > *:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 480px) {
  .cut {
    max-width: 100%;
  }
}

/* LAYOUT: COLUMNS */

.cols {
  display: -webkit-flex;
  -webkit-flex-wrap: wrap;
  display: flex;
  flex-wrap: wrap;
}

.half {
  width: calc(50% - 1.5em);
  padding-right: 1.5em;
}

.third {
  width: calc(33.33% - 1.5em);
  padding-right: 1.5em;
}

.quarter {
  width: calc(25% - 1.5em);
  padding-right: 1.5em;
}

.two-thirds {
  width: calc(66.66% - 1.5em);
  padding-right: 1.5em;
}

.home-bio {
  font-size: 95%;
  line-height: 1.5em;
}

@media screen and (max-width: 720px) {
  .half {
    width: 100%;
    padding-right: 0;
  }

  .third {
    width: 100%;
    padding-right: 0;
  }

  .quarter {
    width: 50%;
    padding-right: 0;
  }
}

.sep:before {
  content: "";
  display: block;
  position: relative;
  width: 100px;
  left: -1.5em;
  height: 1px;
  /* border-bottom: 2px solid var(--color-slate); */
  border-bottom: 2px solid rgba(39, 49, 66, 0.5);
}

@media screen and (max-width: 640px) {
  .sep {
    display: none;
  }
}

/* ELEMENTS: ICONS */

.fa {
  padding: 6px;
  margin-right: 3px;
  font-size: 100% !important;
  text-align: center;
  text-decoration: none;
  background: black;
  color: white;
  border-radius: 60%;
  transition: all 0.25s ease-in-out;
  opacity: 1;
}

.fa:hover {
  opacity: 0.5;
  transition: all 0.25s;
}

.icon-bar {
  margin-top: -3px;
  margin-right: 30px;
}

/* ELEMENTS: SOCIAL LINKS */

.social-links-prof span {
  display: block;
  margin: 0.25em 0;
}

.social-links-prof span .fa {
  margin-right: 1em;
}

/* PAGE STYLES: HOME */

hr.rounded-divider {
  border-top: 4px solid #bbb;
  border-radius: 1px;
  width: 20%;
}

.home-title {
  text-transform: uppercase;
  font-weight: 600;
}

.home-top {
  display: block;
  height: 1em;
}

.home-desc {
  margin-top: 0.5em;
  margin-bottom: 1em;
  display: block;
  font-weight: 400;
  font-size: 150%;
  line-height: 150%;
}

.home-subdesc {
  margin-top: 1.25em;
  display: block;
  font-weight: 400;
  font-size: 100%;
  line-height: 150%;
}

.home-bottom {
  display: block;
  height: calc(2vh + 1vw);
}

@media screen and (max-width: 640px) {
  .home-top {
  }

  .home-desc {
    line-height: 125%;
    font-size: 150%;
  }

  .home-subdesc {
    font-size: 100%;
  }
}

/* PAGE STYLES: BIO */

#bio-prof-pic {
  border-radius: 50%;
  width: 320px;
  float: right;
  margin: 0px 0 24px 20px;
}

#iceland-pic {
  border-radius: 50%;
  width: 320px;
  float: left;
  margin: 0px 0 24px 20px;
}

#cuba-pic {
  width: 200px;
}

@media screen and (max-width: 768px) {
  #bio-prof-pic {
    float: none;
    margin: 0;
    margin-bottom: 20px;
    width: 280px;
  }
}

/* PAGE STYLES: WORK */

.projects {
  margin-bottom: 12px;
}

.projects > * {
  background-color: #f5f8fc;
}

.projects > * > *:last-child {
  margin-bottom: 24px;
}

.projects > * > *:not(img) {
  margin-left: 24px;
  margin-right: 24px;
  display: block;
}

.projects > * > *:first-child:not(img) {
  margin-top: 24px;
}

.projects .full {
  width: 100%;
}

.projects .full .title {
  margin-top: 18px;
}

.projects .full img {
  height: 100%;
  max-width: 50%;
  object-fit: cover;
  float: left;
  margin-right: 24px;
}

.projects .half {
  width: calc(50% - 6px);
  margin-bottom: 12px;
  padding-right: 0;
}

.projects .half img {
  object-fit: cover;
  height: 240px;
}

.projects .half:nth-child(odd) {
  margin-right: 6px;
}

.projects .half:nth-child(even) {
  margin-left: 6px;
}

.projects .third {
  width: calc(33.333% - 9px);
  margin-bottom: 12px;
  padding-right: 0;
}

.projects .third:nth-child(3n + 1) {
  margin-right: 6px;
}

.projects .third:nth-child(3n + 2) {
  margin-right: 6px;
  margin-left: 6px;
}

.projects .third:nth-child(3n) {
  margin-left: 6px;
}

.projects .third .subtitle {
  margin-top: -6px;
  margin-bottom: 18px;
}

.projects .title {
  font-size: 120%;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1em;
  margin-top: 24px !important;
  margin-bottom: 18px;
}

.projects .subtitle {
  font-weight: bold;
  margin-top: 6px;
  margin-bottom: 6px;
  line-height: 1em;
}

.projects .description {
  margin-top: 18px;
  font-size: 85%;
  line-height: 1.25em;
}

.projects .label {
  text-transform: uppercase;
  font-size: 90%;
  font-weight: bold;
  margin-top: 12px;
  margin-bottom: -6px;
}

@media screen and (max-width: 720px) {
  .projects img {
    width: 100% !important;
    height: 270px;
    float: none;
  }

  .projects .full img {
    max-width: 100% !important;
    height: 270px;
    float: none;
  }

  .projects .half {
    width: 100%;
  }

  .projects .half:nth-child(odd) {
    margin-right: 0;
  }

  .projects .half:nth-child(even) {
    margin-left: 0;
  }
}

/* CAROUSEL */

.carousel-control {
  text-align: center;
}

/* PHOTOS COVER PAGE */

.inset.photo-cover {
  position: relative;
  height: 66vh;
}

.photo-cover .photo-link-bg {
  position: absolute;
  top: 1.5em;
  left: 0;
  height: 66vh;
  width: 100%;
  z-index: 2002;
}

.photo-cover img {
  height: 66vh;
  width: 100%;
  object-fit: cover;
  background-position: center;
  background-size: cover;
}

.photo-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 1.5em;
  height: 66vh;
  width: calc(100% - 6em);
  z-index: 2001;
}

@media screen and (max-width: 640px) {
  .photo-overlay {
    width: 100%;
    top: 0;
  }
}

.inset.photo-cover .photo-text {
  position: absolute;
  max-width: 480px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2003;
  color: white;
  text-align: center;
}
